	import {
		debounce
	} from '@/common/utils.js';
	import wygBottomTab from '@/components/wyg-bottom-tab.vue';
	import TAB_LIST from '../../static/wyg-bottom-tab/js/botoom-list.js'
	export default {
		data() {
			return {
				phone: '',
				categart: '',
				tabList: TAB_LIST,
				user: false,
				loginStatus: true
			};
		},
		components: {
			wygBottomTab
		},
		mounted() {
			var _this = this;
			// this.token=uni.getStorageSync('token');
			// this.phone=uni.getStorageSync('phone');
			if (this.global_.token) {
				this.loginStatus = false;
				var p = uni.getStorageSync('phone');
				var p1 = p.split(''); //字符串转为数组
				var phone =
					p1
					.slice(0, 3)
					.join()
					.replace(/,/g, '') +
					'****' +
					p1
					.slice(7, 11)
					.join()
					.replace(/,/g, '');
				_this.phone = phone;
				console.log(_this.phone)
				uni.request({
					url: this.url + 'home/usersviews/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						console.log(res)
						//字段名字就是categart这里面有三个状态 1,普通用户   2,经销商  3,公司   4,销售员   5,投资人
						_this.categart = res.data.data;
					}
				});
			} else {
				this.loginStatus = true;
			}
           // console.log(uni.getStorageSync('phone'))
			
		},

		methods: {
			login: function() {
				this.linkToTransfer10();
			},
			linkToTransfer10: debounce(
				function() {
					uni.navigateTo({
						url: '../login/login'
					});
				},
				1000,
				true
			),
			linkToTransfer4: debounce(
				function() {
					if (this.global_.token) {
						uni.navigateTo({
							url: '../../my/power/power'
						});
					} else {
						uni.showToast({
							title: '您还没有登录',
							icon: 'none',
							duration: 2000
						})
					}

				},
				1000,
				true
			),
			power: function() {
				this.linkToTransfer4();
			},
			personal: function() {
				uni.navigateTo({
					url: '../../my/personal/personal?phone=' + this.phone
				});
			},
			linkToTransfer: debounce(
				function() {
					if (this.global_.token) {
						uni.navigateTo({
							url: '../../my/my-wallet/my-wallet'
						});
					} else {
						uni.showToast({
							title: '您还没有登录',
							icon: 'none',
							duration: 2000
						})
					}

				},
				1000,
				true
			),
			wallet: function() {
				this.linkToTransfer();
			},
			linkToTransfer2: debounce(
				function() {
					uni.navigateTo({
						url: '../../my/machine-sale/machine-sale'
					});
				},
				1000,
				true
			),
			machinsale: function() {
				this.linkToTransfer2();
			},
			linkToTransfer3: debounce(
				function() {
					if (this.global_.token) {
						uni.request({
							url: this.url + 'walletaddresss/',
							method: 'GET',
							header: {
								Authorization: 'JWT' + ' ' + this.global_.token
							},
							success(res) {
								if (res.statusCode == 400) {
									uni.showToast({
										title: '用户未实名认证',
										icon: 'none',
										duration: 2000
									});
								}
								if (res.statusCode == 201) {
									uni.showToast({
										title: '身份认证审核中，请等待',
										icon: 'none',
										duration: 2000
									});
								}
								if (res.statusCode == 200) {
									uni.navigateTo({
										url: '../../my/address/address'
									});
								}
								if (res.statusCode == 302) {
									uni.showToast({
										title: '用户未设置交易密码',
										icon: 'none',
										duration: 2000
									});
								}
							}
						});
					} else {
						uni.showToast({
							title: '您还没有登录',
							icon: 'none',
							duration: 2000
						})
					}

				},
				1000,
				true
			),
			address: function() {
				this.linkToTransfer3();
			},
			tradePassword: function() {
				var that = this;
				uni.request({
					url: this.url + 'setmoneys/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 302) {
							uni.showToast({
								title: '用户未绑定邮箱',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 400) {
							uni.navigateTo({
								url: '../../my/change-password/change-password'
							});
						}
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/trade-password/trade-password'
							});
						}
					}
				});
			},
			loginPassword: function() {
				uni.request({
					url: this.url + 'updataloginpasswords/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 400) {
							uni.navigateTo({
								url: '../../my/change-loginPassword/change-loginPassword'
							});
						}
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/change-pass/change-pass'
							});
						}
					}
				});
			},
			bindEmail: function() {
				uni.request({
					url: this.url + 'linkemails/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 200 || res.statusCode == 201) {
							uni.navigateTo({
								url: '../../my/email/email'
							});
						}
						if (res.statusCode == 400) {
							uni.navigateTo({
								url: '../../my/unbindemail/unbindemail'
							});
						}
					}
				});
			},
			linkToTransfer9: debounce(
				function() {
					if (this.global_.token) {
						uni.navigateTo({
							url: '../../my/suggest/suggest'
						});
					} else {
						uni.showToast({
							title: '您还没有登录',
							icon: 'none',
							duration: 2000
						})
					}
				},
				1000, true
			),
			suggest: function() {
				this.linkToTransfer9()
			},
			help: function() {
				uni.showToast({
					title: '暂未开放',
					icon: 'none',
					duration: 2000
				});
			},
			certification: function() {
				uni.request({
					url: this.url + 'realnames/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success: function(res) {
						if (res.statusCode == 202) {
							uni.showToast({
								title: '已实名认证',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 205) {
							uni.showToast({
								title: '身份认证审核中，请等待',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/identity/identity'
							});
						}
					}
				});
			},
			linkToTransfer5: debounce(
				function() {
					uni.navigateTo({
						url: '../../my/my-machine/my-machine'
					});
				},
				1000,
				true
			),
			mymachine: function() {
				this.linkToTransfer5();
			},

			linkToTransfer6: debounce(function() {
				uni.navigateTo({
					url: '../../my/coupon/coupon'
				});
			}, 1000, true),
			coupon: function() {
				this.linkToTransfer6()
			},
			linkToTransfer8: debounce(function() {
				if (this.global_.token) {
					uni.navigateTo({
						url: '../set/set'
					});
				} else {
					uni.showToast({
						title: '您还没有登录',
						icon: 'none',
						duration: 2000
					})
				}

			}, 1000, true),
			set: function() {
				this.linkToTransfer8()
			},
			lioj: function() {
				uni.navigateTo({
					url: '../malse/malse'
				})
			}
		}
	};
