import wygBottomTab from '@/components/wyg-bottom-tab.vue';
import TAB_LIST from '../../static/wyg-bottom-tab/js/botoom-list.js'
import {
	debounce
} from '@/common/utils.js'
export default {
	data() {
		return {
			n: true,
			pool: '',
			token:uni.getStorageSync('token'),
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '最新'
				},
				{
					state: 1,
					text: '排行'
				},
				{
					state: 2,
					text: '已购买'
				}
			],
			flag: true,
			tabList: TAB_LIST,
			laber: '',
			outer: '',
			toounre: '',
			lists: '',
			my_cunli: '0.00',
			cunli_trans: '0.00',
			len: ''
		};
	},

	mounted() {
		var that = this;
		uni.request({
			url: this.url + 'cloudinfos/',
			method: 'GET',
			success: res => {
				console.log(res)
				that.pool = res.data.message;
			}
		});
		if(that.token){
			uni.request({
				url: this.url + 'cloudareadys/',           //已购存力接口
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success: res => {
					console.log(res)
					var poollists = res.data.data;
					this.lists = poollists.reverse()
					this.len = this.lists.length;
					this.my_cunli = res.data.num
					this.cunli_trans = res.data.fil_nums
					if (this.lists.length != 0) {
						that.flag = false
					}
				}
			})
		}
	
		
	},
	components: {
		wygBottomTab
	},
	methods: {
		trans: function() {
			uni.showToast({
				title: '存力转让功能暂未开放',
				icon: 'none',
				duration: 2000
			})
		},
		next: function() {
			this.n = false;
		},
		back: function() {
			this.n = true;
		},
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			var that = this;
			that.tabCurrentIndex = index;
		},
		linkToTransfer: debounce(function(item) {
			var d = JSON.stringify(item)
			uni.navigateTo({
				url: '../poolDetails/poolDetails?data=' + d
			});
		}, 500, true),
		buy: function(item) {
			this.linkToTransfer(item)
		}
	}
};
